.hljs
{
    display: block;
    overflow-x: auto;

    padding: .5em;

    background: #000;
}.hljs,
.hljs-subst
{
    color: #0ff;
}.hljs-string,
.hljs-attribute,
.hljs-symbol,
.hljs-bullet,
.hljs-built_in,
.hljs-builtin-name,
.hljs-template-tag,
.hljs-template-variable,
.hljs-addition
{
    color: #ff0;
}.hljs-keyword,
.hljs-selector-tag,
.hljs-section,
.hljs-type,
.hljs-name,
.hljs-selector-id,
.hljs-selector-class,
.hljs-variable
{
    color: #fff;
}.hljs-comment,
.hljs-quote,
.hljs-doctag,
.hljs-deletion
{
    color: #888;
}.hljs-number,
.hljs-regexp,
.hljs-literal,
.hljs-link
{
    color: #0f0;
}.hljs-meta
{
    color: #008080;
}.hljs-keyword,
.hljs-selector-tag,
.hljs-title,
.hljs-section,
.hljs-name,
.hljs-strong
{
    font-weight: bold;
}.hljs-emphasis
{
    font-style: italic;
}
